<template>
  <div class="user" v-if="user">
    <div class="user__left">
      <img :src="user.avatar_url" alt="">
      <a :href="user.html_url" class="user__link">ПОСЕТИТЬ</a>
    </div>
    <div class="user__right">
      <p>{{ user.login }}</p>
      <p>Репозиториев: <span>{{ user.public_repos }}</span></p>
      <p>Создан: <span>{{ new Date(user.created_at).toLocaleDateString() }}</span></p>
      <p>Подписщиков: <span>{{ user.followers }}</span></p>
      <p>Подписок: <span>{{ user.following }}</span></p>
    </div>
  </div>
  <div class="sort">
    <h2 class="sort__title">Сортировка</h2>
    <div class="sort__box">
      <button 
      class="sort__box-btn" 
      v-for="(btn, index) in btns"
      :key="btn.name"
      :class="{active: isActive ==  index}"
      @click="sort(index, btn.type)"
      >
      {{btn.name}}
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data(){
    return{
      isActive: 0,
      btns: [
        {name: 'ИМЯ', type: 'name'},
        {name: 'ЗВЕЗДЫ', type: 'stargazers_count'},
        {name: 'ДАТА', type: 'created_at'},
      ]  
    }
  },
  computed:{
    ...mapState(['user'])
  },
  methods:{
    ...mapMutations(['sortRepos']),
    sort(index, type){
      this.isActive = index
      this.sortRepos(type)
    }
  }
}
</script>

