<template>
  <div class="search">
    <div class="search__box">
        <input 
        type="text" 
        class="search__input" 
        placeholder="Введите имя пользователя"
        v-model="search"
        @keydown.enter="getUserInfo"
        >
        <span class="search__clear" @click="clear">❌</span>
    </div>
    <button class="search__btn" @click="getUserInfo"> НАЙТИ</button>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";
export default {
  data(){
    return{
      search: ''
    }
  },
  methods:{
    ...mapActions(['getInfo']),
    ...mapMutations(['resetUser']),
    getUserInfo(){
      this.getInfo(this.search)
    },
    clear(){
      this.search = ''
      this.resetUser()
    }
  }
}
</script>

