import { createStore } from "vuex";
import axios from "axios";

const store = createStore({
  state: {
    user: null,
    repositories: null,
    url: "https://api.github.com/users/",
    error: null,
    currentSort: "name",
  },
  mutations: {
    sortRepos(state, payload) {
      state.currentSort = payload;
    },
    getInfo(state, payload) {
      state.user = payload;
      state.error = null;
    },
    resetUser(state) {
      state.user = null;
      state.error = null;
    },
    reposInfo(state, payload) {
      state.repositories = payload;
    },
    error(state, payload) {
      if (payload.response.status == 403) {
        state.error = "Вас забанили на определенное время";
      } else {
        state.error = "Ошибка с сервером";
      }
    },
  },
  actions: {
    async getInfo({ commit, state }, search) {
      try {
        const res = await axios.get(`${state.url}${search}`);
        const repos = await axios.get(`${state.url}${search}/repos`);
        commit("getInfo", res.data);
        commit("reposInfo", repos.data);
      } catch (error) {
        commit("error", error);
        console.log("Произошла ошибка при получении данных");
      }
    },
  },
  getters: {
    getReposSort(state) {
      if (state.repositories != null) {
        return state.repositories.sort((prev, next) => {
          let mod = 1
          if (prev[state.currentSort] < next[state.currentSort]) {
            return -1 * mod
          }
        });
      }
    },
  },
});
export default store;
